  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
